@media screen and (max-width: 1313px) {
  .form__content {
    justify-content: center;
    margin-left: 0;
  }
}

@media screen and (max-width: 1182px) {
  .form {
    padding-bottom: 10rem;
  }
  .form__content {
    justify-content: space-evenly;
    gap: 8rem;
  }
}

@media screen and (max-width: 400px) {
  .form__img {
    max-height: 30rem;
  }

  .form__input {
    width: 80%;
    min-width: 35rem;
  }
}
