@media screen and (max-width: 1530px) {
  .moment {
    justify-content: center;
    background: #00000029;
  }

  .moment__title,
  .moment__paragraph {
    text-align: center;
  }

  .moment__btns {
    display: flex;
    justify-content: center;
    gap: 1rem;
    flex-wrap: wrap-reverse;
  }
}
